.card-slider-wrap {
    position: absolute;
    right: 0;
    bottom: 30px;
    width: 50%;
}

.players-row {
    position: relative;
    overflow: hidden;
    width: calc(100% - 9px);
    left: 4px;
    height: 100%;

    .number {
        color: #fff;
        position: absolute;
        top: 62px;
        left: 0;
        text-align: center;
        width: 61px;
        font-weight: bold;
        z-index: 2;
        text-shadow: 0px 0px 3px rgba(0, 0, 0, .5);
        font-size: 37px;
        font-family: 'INGAME', sans-serif;
    }

    .position {
        font-size: 26px;
        color: #fff;
        position: absolute;
        top: 99px;
        left: 0;
        text-align: center;
        width: 61px;
        z-index: 2;
        text-shadow: 0px 0px 3px rgba(0, 0, 0, .5);
        font-family: 'INGAME', sans-serif;
        font-weight: 100;
        letter-spacing: -1px;
    }

    .country-logo {
        position: absolute;
        top: 133px;
        left: 15px;
        width: 32px;
        text-align: center;
        z-index: 2;
    }

    .img {
        position: absolute;
        top: 81px;
        right: 5px;
        z-index: 1;
        width: 155px;
        height: 169px;
        overflow: hidden;

        img {
            width: 169px;
        }
    }

    .team-logo {
        position: absolute;
        top: 192px;
        left: 9px;
        text-align: center;
        width: 46px;
        height: 46px;
        z-index: 2;
        display: flex;
        overflow: hidden;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        background-color: #fff;

        img {
            width: 33px;
            height: 33px;
        }
    }

    .players {
        width: 100%;
        position: absolute;
        top: 262px;
        left: 0;
        display: flex;
        justify-content: center;
        align-items: center;

        img {
            width: 20px;
            height: 16px;
            margin-right: 3px;
            display: inline-block;
            vertical-align: top;
        }

        .name {
            display: inline-block;
            max-width: 140px;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            font-size: 19px;
            font-weight: 500;
            color: #141432;
            font-family: "Pretendard-Regular", sans-serif;
        }
    }
}

.swiper-pagination {
    position: absolute;
    bottom: 17px !important;
    left: auto !important;
    right: 0 !important;
    width: 100%;
    text-align: center;
    z-index: 10;
}

.swiper-horizontal {
    padding-bottom: 90px;
}

.swiper-pagination {
    width: 83% !important;
}

.swiper-pagination-bullet {
    display: inline-block;
    width: 3.8%;
    height: 4px;
    background-color: #000;
    border-radius: 0;
    margin: 0 !important;
}

.swiper-pagination-bullet-active {
    animation: neonGlow 1.5s infinite alternate;
}

.swiper-button-prev, .swiper-button-next {
    position: absolute;
    top: auto;
    bottom: 0; /* Adjust as needed to place below the slider */
    width: 50px; /* Button size */
    height: 50px; /* Button size */
    background-color: transparent; /* Circle background color */
    border: 2px solid #ccc; /* Circle border */
    border-radius: 50%; /* Makes it circular */
    color: #ccc; /* Arrow color */
    font-size: 20px; /* Arrow size, adjust as needed */
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 10;
    cursor: pointer; /* Makes it obvious it's clickable */
    outline: none; /* Removes the outline to clean up the appearance */
}

.swiper-button-prev {
    left: 0;
}

.swiper-button-next {
    left: 60px;
}

/* 화살표 스타일 */
.swiper-button-prev::after, .swiper-button-next::after {
    font-size: 1.2em; /* Adjust arrow size */
}

/* 마우스 호버 시 효과 */
.swiper-button-prev:hover, .swiper-button-next:hover {
    background-color: rgba(255, 255, 255, 0.2); /* Light white background on hover */
}